export enum CipherType {
  ONE_TIME_PAD = 'ONE_TIME_PAD',
}

export type OneTimePadEncodeOptions = {
  type?: CipherType.ONE_TIME_PAD;
  format?: boolean;
};

export type OneTimePadDecodeOptions = {
  type?: CipherType.ONE_TIME_PAD;
};

export type CipherEncodeOptions = OneTimePadEncodeOptions;

export type CipherDecodeOptions = OneTimePadDecodeOptions;

export type CipherEncodeFunc = (
  plaintext: string,
  secret: string,
  options?: CipherEncodeOptions
) => string;

export type CipherDecodeFunc = (
  ciphertext: string,
  secret: string,
  options?: CipherDecodeOptions
) => string;
