/**
 * One Time Pad cipher implementation
 *
 * @seealso https://www.geeksforgeeks.org/implementation-of-vernam-cipher-or-one-time-pad-algorithm/
 */
import { CipherDecodeFunc, CipherEncodeFunc } from '../types';

export const VALIDATION_ERROR = 'VALIDATION_ERROR';

export const SECRET_LENGTH_ERROR = 'SECRET_LENGTH_ERROR';

export const alphabet: any = {
  A: '00',
  B: '01',
  C: '02',
  D: '03',
  E: '04',
  F: '05',
  G: '06',
  H: '07',
  I: '08',
  J: '09',
  K: '10',
  L: '11',
  M: '12',
  N: '13',
  O: '14',
  P: '15',
  Q: '16',
  R: '17',
  S: '18',
  T: '19',
  U: '20',
  V: '21',
  W: '22',
  X: '23',
  Y: '24',
  Z: '25',
};

export const alphabetKeys: string[] = Object.keys(alphabet);

const reversedAlphabet: any = {
  '00': 'A',
  '01': 'B',
  '02': 'C',
  '03': 'D',
  '04': 'E',
  '05': 'F',
  '06': 'G',
  '07': 'H',
  '08': 'I',
  '09': 'J',
  '10': 'K',
  '11': 'L',
  '12': 'M',
  '13': 'N',
  '14': 'O',
  '15': 'P',
  '16': 'Q',
  '17': 'R',
  '18': 'S',
  '19': 'T',
  '20': 'U',
  '21': 'V',
  '22': 'W',
  '23': 'X',
  '24': 'Y',
  '25': 'Z',
};

/**
 * Format a text input to contain only alphabet characters.
 * This is done by simply transforming it to "uppercase" & removing non-alphabet characters.
 *
 * @param text
 * @returns a text containing only alphabet characters
 */
export const formatText = (text: string) => {
  text = text.toUpperCase();

  let formattedText: string = '';

  for (let index = 0; index < text.length; index++) {
    const char: string = text[index];

    if (alphabetKeys.includes(char)) {
      formattedText += char;
    }
  }

  return formattedText;
};

export const validateText = (text: string) => {
  for (let index = 0; index < text.length; index++) {
    const char = text[index];

    if (!alphabetKeys.includes(char)) {
      throw new Error(VALIDATION_ERROR);
    }
  }
};

export const validateSecretLength = (
  plaintext: string,
  secret: string
): void => {
  const requiredSecretLength = plaintext.length;

  if (secret.length < requiredSecretLength) {
    throw new Error(SECRET_LENGTH_ERROR);
  }
};

/**
 * Split a text input into fixed size chunks.
 *
 * @param text
 * @param chunkSize
 * @param options
 * @returns text chunks
 */
const chunkText = (
  text: string,
  chunkSize: number = 1,
  options?: { padEnd?: boolean; fillString?: string }
): string[] => {
  const result: string[] = [];

  for (let i = 0; i < text.length; i += chunkSize) {
    let chunk: string = text.substring(i, i + chunkSize);

    if (chunk.length > 0) {
      if (options?.padEnd) {
        chunk = chunk.padEnd(chunkSize, options?.fillString);
      }

      result.push(chunk);
    }
  }

  return result;
};

const add = (a: number, b: number) => {
  const sum = a + b,
    max = alphabetKeys.length;

  if (sum > max) {
    return sum - max;
  }

  return sum;
};

const subtract = (a: number, b: number) => {
  const subtraction = a - b,
    max = alphabetKeys.length;

  if (subtraction < 0) {
    return subtraction + max;
  }

  return subtraction;
};

const encodeNumber = (number: number): string => {
  if (number < 10) return `0${number}`;

  return String(number);
};

const encodeText = (text: string): string =>
  chunkText(text)
    .map((chunk: string) => alphabet[chunk])
    .join('');

// TODO: implement options
export const oneTimePadEncode: CipherEncodeFunc = (
  plaintext: string,
  secret: string
): string => {
  validateText(plaintext);

  validateText(secret);

  validateSecretLength(plaintext, secret);

  const encodedPlaintext: string = encodeText(plaintext),
    encodedSecret: string = encodeText(secret);

  const plaintextSequence: number[] = chunkText(
      encodedPlaintext,
      2
    ).map((chunk: string) => Number(chunk)),
    secretSequence: number[] = chunkText(
      encodedSecret,
      2
    ).map((chunk: string) => Number(chunk));

  return plaintextSequence
    .map((element: number, index: number) =>
      encodeNumber(add(element, secretSequence[index]))
    )
    .join('');
};

export const oneTimePadDecode: CipherDecodeFunc = (
  ciphertext: string,
  secret: string
) => {
  const ciphertextSequence: number[] = chunkText(
      ciphertext,
      2
    ).map((chunk: string) => Number(chunk)),
    secretSequence: number[] = chunkText(
      encodeText(secret),
      2
    ).map((chunk: string) => Number(chunk));

  return ciphertextSequence
    .map((element: number, index: number) =>
      encodeNumber(subtract(element, secretSequence[index]))
    )
    .map((element: string) => reversedAlphabet[element])
    .join('');
};
